.jzxyBS {
  margin: 0px;
}
.hKPiiD {
  background: rgb(255, 255, 255);
  padding: 0px 16px;
  color: rgb(34, 35, 34);
  box-sizing: border-box;
  font-size: 16px;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5;
  letter-spacing: normal;
  max-width: 674px;
  margin: auto;
  font-weight: normal !important;
}
.iPXGFB {
  text-align: center;
  padding-top: 10px;
}
.iPXGFB button {
  margin: 0px 20px;

  display: inline-block;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: normal !important;
}
.gaXMVp {
  text-align: left;
  margin: 40px 0px;
}
.gaXMVp h5 {
  font-size: 16px;
  display: inline-block;
  margin: 0px 30px 8px 0px;
  color: #b55928;
  width: 100px;
  font-weight: normal !important;
}
.gaXMVp select {
  display: inline-block;
  width: 100%;
  height: 56px;
  border: 2px solid rgb(152, 152, 152);
  border-radius: 30px;
  color: rgb(34, 35, 34);
  appearance: none;
  font-size: 18px;
  background-color: rgb(255, 255, 255);

  padding: 3px 50px 0px 25px;
  overflow: hidden;
  cursor: pointer;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 14px 11px;
  background-repeat: no-repeat;
  background-position: calc(100% - 20px) center;
  font-weight: normal !important;
}
.dcXACD {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
}
.dcXACD h5 {
  display: inline-block;
  margin: 0px;
  font-size: 16px;
  color: #b55928;
  font-weight: normal !important;
}
.dcXACD .filterGroup {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0px;
  align-items: flex-start;
  row-gap: 8px;
}
.dcXACD button.filterButton {
  width: 25%;
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  background: rgb(255, 255, 255);
  border: none;
  margin: 0px;
  cursor: pointer;
}
.dcXACD .iconWrapper {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  max-width: 78px;
}
.dcXACD button span {
  margin-top: 10px;
  color: rgb(34, 35, 34);
  width: 110%;
  text-align: center;
  font-size: 12px;
  max-width: 80px;
  font-weight: normal !important;
}
.dcXACD .iconWrapper svg {
  width: 26px;
  height: 26px;
}
.ceBYQd {
  margin-bottom: 16px;
}
.jMHQDw {
  color: #b55928;
  line-height: 24px;
  font-size: 16px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 40px 12px 16px;
  border-radius: 8px;
  background-color: rgb(240, 240, 240);
  cursor: pointer;
  user-select: none;
  margin: 0px;
  appearance: none;
  border: 0px;
  width: 100%;
  font-weight: normal !important;
}
.gCkLSd {
  position: absolute;
  right: 16px;
  top: 17px;
  transition: transform 0.3s ease 0s;
  transform: rotate(0deg);
}
.caKoHX {
  font-weight: normal !important;
}
.caKoHX .calorieMessageWrapper {
  display: flex;
  flex-direction: column;
  margin: 0px;
}
.caKoHX .calorieMessageWrapper h5 {
  font-size: 16px;
  color: #b55928;
  font-weight: normal !important;
}
.caKoHX .calorieMessageWrapper p {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
}
.caKoHX .calorieMessageWrapper p:last-child {
  padding-top: 38px;
}
.caKoHX .calorieMessageIcon {
  padding-right: 10px;
}
.jhTJPl .categoryHeader {
  font-size: 36px;
  line-height: 1.1;
  color: #b55928;
  margin: 48px 0px 32px;
  font-weight: normal !important;
}
.jhTJPl .container {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
.jxXUwO {
  min-height: 244px;
  width: 50%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  outline: none;
  appearance: none;
}
.jxXUwO .image {
  height: 150px;
  display: flex;
  justify-content: center;
}
.jxXUwO .image img {
  width: auto;
  height: 100%;
}
.jxXUwO .header {
  font-size: 16px;
  color: #b55928;
  margin-bottom: 9px;
  padding: 0px 10px;
  text-align: center;
  overflow-wrap: break-word;
  max-width: 200px;
  font-weight: normal !important;
}

